import React, { useRef } from 'react';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import { useEffect } from 'react';

const Blog = () => {
    const halloweenRef = useRef(null);
    const magostoRef = useRef(null);
    const belenRef = useRef(null);
    const carnavalRef = useRef(null);
    const excursionesRef = useRef(null);
    const diaRef = useRef(null);
    


    const scrollToSection = (ref) => {
        ref.current?.scrollIntoView({ behavior: 'smooth' });
      };

      const location = useLocation();

      useEffect(() => {
          const section = document.getElementById(location.hash.replace('#', ''));
          if (section) {
              section.scrollIntoView({ behavior: 'smooth' });
          }
      }, [location]);

  return (
    <div>
         <div id="top" className="container-fluid mb-5" style={{background: '#EFE4D7' }}>
            <div className="d-flex flex-column align-items-center justify-content-center" style={{minHeight: "400px"}}>
                <h3 className="display-3 font-weight-bold" style={{color:'#563d26'}}>Blog</h3>
                <div className="d-inline-flex" style={{color:'#563d26'}}>
                <p className="m-0"><Link to="/" style={{color:'#563d26', textDecoration: "none", fontWeight: 'bold'}}>Home</Link></p>
                    <p className="m-0 px-2">/</p>
                    <p className="m-0"  style={{color:'#563d26', fontWeight: 'bold'}}>Detalles Blog</p>
                </div>
            </div>
        </div>
    
        <div className="container py-5">
            <div className="row pt-5">
                <div className="col-lg-8">
                    <div className="d-flex flex-column text-left mb-3">
                        <p className="section-title pr-5"><span className="pr-2" style={{color:'#d3ad76'}}>Detalles Del Blog</span></p>
                        <div id="halloween" ref={halloweenRef}>
                            <h1 className="mb-3" style={{color:"#940013"}}>Halloween: diversin y Creatividad para Todos.</h1>
                    </div>
                            <div className="mb-5">
                            <img className="img-fluid rounded w-100 mb-4" src={`${process.env.PUBLIC_URL}/img/halloween2.png`} alt="Foto"/>
                            <p style={{ textAlign:"justify"}}>En el CEIP de la Barquera, la fiesta de Halloween es una de las celebraciones ms esperadas por los nios y las familias. Durante toda una semana, los pasillos y aulas se llenan de decoraciones temticas y actividades especiales. Los alumnos, junto con sus profesores, preparan sus disfraces y decoran el colegio, transformndolo en un espacio espeluznante y lleno de sorpresas.</p>
                            <p style={{ textAlign:"justify"}}>Cada clase participa en un concurso de calabazas, donde los nios muestran su creatividad al tallarlas y decorarlas, hay autnticas obras de arte! Adems, el famoso Trasno Trouleiro, una figura muy querida por los nios, hace su aparicin, dejando pistas y regalitos sorpresa en cada rincn del colegio. Como cierre, celebramos el tradicional Magosto, en el que asamos castaas y las compartimos en familia, creando un ambiente acogedor y festivo.</p>
                            <p style={{ textAlign:"justify"}}>Halloween en nuestro cole no solo es una oportunidad para la diversin, sino tambin para aprender a trabajar en equipo y fortalecer la unin entre todos. Un evento inolvidable para grandes y pequeos!</p>
                        </div>
                        <div id="magosto" ref={magostoRef}>
                            <h2 className="mb-4" style={{color:"#940013"}}>Magosto: una Tradicin Gallega que Nos Une</h2>
                            <img className="img-fluid rounded w-50 float-left mr-4 mb-3" src={`${process.env.PUBLIC_URL}/img/castaas3.png`} alt="Foto"/>
                            <p style={{ textAlign:"justify"}}>Cada otoo, el CEIP de la Barquera celebra el Magosto, una de las tradiciones ms queridas y esperadas en Galicia. La preparacin comienza con la recoleccin de castaas, que los nios traen al colegio junto a sus familias. El da del Magosto, el patio se convierte en un autntico espacio de fiesta y tradicin, donde todos se renen para asar las castaas en una hoguera al aire libre.</p>
                            <p style={{ textAlign:"justify"}}>En esta jornada, las familias estn invitadas a compartir con los nios y el equipo docente, disfrutando juntos de las castaas asadas. Adems de la degustacin, el colegio organiza juegos tradicionales y actividades al aire libre, llenando el da de momentos de diversin y camaradera. Para muchos, es un da especial para reconectar con la cultura gallega y para que los ms pequeos comprendan el valor de nuestras tradiciones.</p> 
                            <p style={{ textAlign:"justify"}}>El Magosto en el CEIP de la Barquera no solo celebra el otoo y las costumbres de nuestra tierra, sino que tambin fortalece el lazo entre familias, alumnos y profesores. Sin duda, una tradicin que contina aportando calidez y recuerdos inolvidables a nuestra comunidad educativa.</p>
                        </div>
                        <div id="belen" ref={belenRef}>
                            <h3 className="mb-4" style={{color:"#940013"}}>Beln Viviente: la Magia de la Navidad </h3>
                            <img className="img-fluid rounded w-50 float-right ml-4 mb-3" src={`${process.env.PUBLIC_URL}/img/belen3.png`} alt="Foto"/>
                            <p style={{ textAlign:"justify"}}>Cada diciembre, el CEIP de la Barquera se transforma para recibir el espritu navideo con su ya tradicional Beln Viviente. Los alumnos son los protagonistas de esta celebracin, representando a los personajes del nacimiento en un escenario preparado especialmente para la ocasin. Desde pastores hasta artesanos, todos los roles son interpretados por los nios, quienes aportan entusiasmo y autenticidad a la recreacin.</p>
                            <p style={{ textAlign:"justify"}}>El Beln Viviente se convierte en un momento de encuentro entre toda la comunidad escolar. Las familias son invitadas a presenciar la obra, disfrutando de la emocin de sus hijos mientras representan esta historia clsica de Navidad. Los docentes se involucran en la preparacin y los detalles de cada escena, logrando un ambiente nico y lleno de ilusin.</p>
                            <p style={{ textAlign:"justify"}}>Adems de la representacin, el colegio se llena de decoraciones navideas y actividades especiales, todo diseado para celebrar estas fechas tan especiales en un ambiente clido y acogedor. Esta actividad no solo fortalece los valores de respeto y colaboracin, sino que tambin ofrece a los nios una experiencia creativa y memorable, creando lazos y recuerdos que perduran.</p>
                        </div>
                </div>
                        <div ref={carnavalRef}>        
                            <h1 className="mb-3" style={{color:"#940013"}}>Carnaval: semana de Colores, Sorpresas y el Trasno Trouleiro</h1>
                <div className="mb-5">
                            <img className="img-fluid rounded w-100 mb-4" src={`${process.env.PUBLIC_URL}/img/carnaval.png`} alt="Foto"/>
                            <p style={{ textAlign:"justify"}}>Cada ao, el Carnaval llena de alegra y creatividad el CEIP de la Barquera. Durante la semana de Carnaval, los nios y nias participan en divertidas actividades temticas: cada da trae un reto diferente, como llevar al cole un sombrero, ponerse calcetines de colores o decorar una mscara. La imaginacin no tiene lmites, y los pasillos del colegio se llenan de disfraces y accesorios que reflejan el entusiasmo de los estudiantes.</p>
                            <p style={{ textAlign:"justify"}}>La fiesta alcanza su punto culminante con la llegada del misterioso Trasno Trouleiro, una figura mtica que se encarga de traer sorpresas para todos. El Trasno deja pistas y regalos en cada aula, haciendo que los nios vivan la emocin de la bsqueda y el descubrimiento. El martes de Carnaval, festivo local en Cerdido, permite a todos disfrutar de la celebracin y preparar con calma sus disfraces ms originales para el gran desfile de la semana siguiente.</p>
                            <p style={{ textAlign:"justify"}}>Este Carnaval, adems de ser una oportunidad para el juego y la diversin, fortalece los lazos entre los compaeros y fomenta la creatividad. Cada rincn del colegio se llena de risas y color, haciendo de esta semana una de las favoritas para todos los nios y las familias del CEIP de la Barquera.</p>
                        </div>
                        <div ref={excursionesRef}> 
                            <h2 className="mb-4"style={{color:"#940013"}}>Excursiones: aventuras y Descubrimientos para Todas las Edades </h2>
                            <img className="img-fluid rounded w-50 float-left mr-4 mb-3" src={`${process.env.PUBLIC_URL}/img/excursion.png`} alt="Foto"/>
                            <p style={{ textAlign:"justify"}}> Las excursiones son una parte esencial de la vida escolar en el CEIP de la Barquera, y estn diseadas para adaptarse a las edades y curiosidades de los estudiantes. Los ms pequeos, entre 3 y 8 aos, disfrutan de salidas locales que los conectan con la naturaleza y la vida rural. Estas excursiones suelen llevarlos a granjas donde interactan con los animales, aprenden sobre el cultivo y viven una jornada educativa al aire libre. La emocin de estos das fomenta su curiosidad y respeto por el entorno.</p>
                            <p style={{ textAlign:"justify"}}> Para los mayores, de 9 a 12 aos, la aventura se vuelve an ms grande. Cada primavera, suelen emprender un viaje de cinco das a un destino especial, como Tenerife o Las Palmas, donde exploran el patrimonio, la cultura y los paisajes nicos de las islas. Esta experiencia no solo les permite descubrir nuevos lugares, sino tambin desarrollar su autonoma y fortalecer los lazos con sus compaeros.</p>
                            <p style={{ textAlign:"justify"}}> Estas excursiones, adaptadas a cada grupo de edad, enriquecen la educacin de los alumnos del CEIP de la Barquera, combinando el aprendizaje con vivencias memorables y acercndolos al mundo que los rodea.</p>
                        </div>
                        <div ref={diaRef}> 
                            <h3 className="mb-4"style={{color:"#940013"}}>Da del libro: celebrando la Magia de Leer</h3>
                            <img className="img-fluid rounded w-50 float-right ml-4 mb-3" src={`${process.env.PUBLIC_URL}/img/libro.png`} alt="Foto"/>
                            <p style={{ textAlign:"justify"}}>El Da del Libro en el CEIP de la Barquera es una jornada llena de actividades para despertar y fortalecer la pasin por la lectura. Cada ao, los nios participan en talleres de cuentacuentos, dramatizaciones de sus historias favoritas y maratones de lectura donde comparten en voz alta los pasajes que ms les inspiran. Adems, es tradicin que cada clase elija un libro especial para leer en grupo, intercambiar impresiones y explorar sus personajes y mensajes en profundidad.</p>
                            <p style={{ textAlign:"justify"}}>Durante este da, los pasillos y aulas se decoran con citas literarias y obras de autores destacados, y los alumnos mayores comparten tiempo con los ms pequeos para leerles cuentos e historias, fomentando un ambiente de colaboracin y amor por los libros. Con esta celebracin, el CEIP de la Barquera reafirma su compromiso con la lectura como pilar fundamental en el aprendizaje y crecimiento de cada estudiante.</p>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 mt-5 mt-lg-0">           
                    <div className="mb-5">
                    </div>            
                    <div className="mb-5">
                        <img src={`${process.env.PUBLIC_URL}/img/cole.png`} alt="Foto" className="img-fluid rounded"/>
                    </div>
                    <div className="mb-5">
                        <h2 className="mb-4" style={{color:"#940013"}}>Atajos</h2>
                        <div className="d-flex flex-wrap m-n1">
                            <button onClick={() => scrollToSection(halloweenRef)} className="btn btn-outline-primary m-1">Halloween</button>
                            <button onClick={() => scrollToSection(magostoRef)} className="btn btn-outline-primary m-1">Magosto</button>
                            <button onClick={() => scrollToSection(belenRef)} className="btn btn-outline-primary m-1">Beln Viviente</button>
                            <button onClick={() => scrollToSection(carnavalRef)} className="btn btn-outline-primary m-1">Carnaval</button>
                            <button onClick={() => scrollToSection(excursionesRef)} className="btn btn-outline-primary m-1">Excusiones</button>
                            <button onClick={() => scrollToSection(diaRef)} className="btn btn-outline-primary m-1">Da del libro.</button>
                        </div>
                    </div>                
                </div>
            </div>
        </div>
        <Link to="#top" className="btn btn-primary p-3 back-to-top" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}><i className="fa fa-angle-double-up"></i></Link>
    </div>
  )
}

export default Blog
